   body {
    font-family: "Lato", sans-serif;
  }
  
  .sidebar {
    height: 100%;
    width: 85px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #02021d;
    overflow-x: hidden;
    transition: 0.4s;
    padding-top: 20px;
    white-space: nowrap;
  }
  
  .sidebar a, .dropdown-container {
    text-decoration: none;
    width: 100%;
    font-size: 25px;
    color: #858490;
    display: block;
  }
  
  .sidebar a:hover {
    color: #d4d3e6;
  }
  
  .side-bar-icon,
  .icon-text {
    vertical-align: middle;
  }
  
  .side-bar-icon {
    margin: 20px 8px 20px 32px;
    margin-right: 35px
  }
  
  .logo-icon {
    width: 60px;
    height: 60px;
    margin-right: 8px;
    margin-bottom: 10px;
    padding: 8px;
    padding-left: 16px;
    vertical-align: middle;

  }

  .logo-text {
    font-size: 32px;
    color: #d4d3e6;
  }

  @media screen and (max-height: 450px) {
    .sidebar {
      padding-top: 15px;
    }
    .sidebar a {
      font-size: 18px;
    }
  }

  html, body{
    padding:0px;
    margin:0px;
    background:#191A1D;
    font-family: 'Karla', sans-serif;
    width:100vw;
 }
 body * {
    margin:0;
    padding:0;
 }
 
