 /* HTML Nav Styles */
 /* HTML Nav Styles */
 nav .dropdown-menu-item {
    position:relative;
    display:block;
    opacity:0;
    cursor:pointer; 
 }
 
 nav .dropdown-menu-item >.dropdown-menu{
    position: absolute;
    pointer-events:none;
 }
 nav >.dropdown-menu{ display:flex; }
 
 nav >.dropdown-menu> .dropdown-menu-item { pointer-events: all; opacity:1; }
.dropdown-menu.dropdown-menu-item a { white-space:nowrap; display:block; }
    
 .dropdown-menu-item:hover >.dropdown-menu{
    pointer-events:initial;
 }
 .dropdown-menu-item:hover >.dropdown-menu> .dropdown-menu-item,
 menu:hover > .dropdown-menu-item{
    opacity:1;
 }

 nav a {
    color:#FFF;
    min-width:190px;
    transition: color 0.3s, transform 0.3s;
    padding-bottom: 20px;
    position:relative;
 }
 

 nav >.dropdown-menu> .dropdown-menu-item >.dropdown-menu> .dropdown-menu-item{
    transition: transform 0.3s, opacity 0.3s;
    transform:translateY(150%);
    opacity:0;
 }

 nav >.dropdown-menu> .dropdown-menu-item:hover >.dropdown-menu> .dropdown-menu-item,
 nav >.dropdown-menu> .dropdown-menu-item.hover >.dropdown-menu> .dropdown-menu-item{
    transform:translateY(0%);
    opacity: 1;
 }

 .tool-item {
   padding-left: 50%;
   font-size: 18px;
 }
 

